<template>
  <div class="back">
    <app-bar
      back-icon
      title="账户设置"
      hide-user-menu
      hideMessage
      hideDemand
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12" min-height="400" max-width="800">
          <v-card-title>账户信息</v-card-title>
          <div class="pl-10">
            <el-form label-position="left" label-width="80px">
              <v-col cols="12" sm="6">
                <el-form-item label="用户名">
                  <el-input readonly v-model="username"></el-input>
                </el-form-item>
                <el-form-item label="账号">
                  <el-input readonly v-model="account"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                  <el-input
                    v-model="password"
                    type="password"
                    readonly
                  ></el-input>
                  <v-dialog v-model="dialog" max-width="400">
                    <template v-slot:activator="{ on }">
                      <el-button type="text" v-on="on">修改密码</el-button>
                    </template>
                    <v-card max-width="400" class="mx-auto">
                      <v-card-title class="headline">修改密码</v-card-title>
                      <v-col>
                        <el-form :rules="rules" ref="formRef" :model="form">
                          <el-form-item prop="oldPwd">
                            <el-input
                              v-model="form.oldPwd"
                              placeholder="当前密码"
                              show-password
                            ></el-input>
                          </el-form-item>
                          <el-form-item prop="newPwd">
                            <el-input
                              v-model="form.newPwd"
                              placeholder="新密码"
                              type="password"
                            ></el-input>
                          </el-form-item>
                          <el-form-item prop="checkPass">
                            <el-input
                              v-model="form.checkPass"
                              placeholder="确认密码"
                              type="password"
                            ></el-input>
                          </el-form-item>
                        </el-form>
                      </v-col>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue lighten-1" text @click="changePwd()"
                          >确认</v-btn
                        >
                        <v-btn color="grey darken-2" text @click="cancel()"
                          >取消</v-btn
                        >
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </el-form-item>
                <el-form-item label="角色">
                  <el-input v-model="role" disabled></el-input>
                </el-form-item>
                <div class="error--text ml-8">{{ msg }}</div>
              </v-col>
            </el-form>
          </div>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'PersonalCenter',
  components: {
    AppBar,
    AppFooter
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.form.checkPass !== '') {
          this.$refs.formRef.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.newPwd) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      dialog: false,
      password: '123456',
      msg: '',
      form: {
        oldPwd: '',
        newPwd: '',
        checkPass: ''
      },
      rules: {
        oldPwd: [
          { required: true, message: '请输入当前密码', trigger: 'blur' }
        ],
        newPwd: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    username() {
      return this.$store.state.username
    },
    account() {
      return this.$store.state.account
    },
    role() {
      let crole = sessionStorage.getItem('role')
      if (crole === 'university') {
        return '高校'
      } else if (crole === 'company') {
        return '企业'
      } else return ''
    }
  },
  methods: {
    async changePwd() {
      let valid = this.$refs.formRef.validate()
      if (!valid) return
      let res = await this.$http.post('/api/user/resetPwd', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.$message.success('修改密码成功！')
        this.$refs.formRef.resetFields()
        this.form.oldPwd = ''
        this.form.newPwd = ''
        this.form.checkPass = ''
        this.dialog = false
      } else {
        this.$message.error('修改密码失败！')
        this.$refs.formRef.resetFields()
        this.form.oldPwd = ''
        this.form.newPwd = ''
        this.form.checkPass = ''
        this.msg = res.data.msg
      }
    },
    cancel() {
      this.dialog = false
      this.form.oldPwd = ''
      this.form.newPwd = ''
      this.form.checkPass = ''
      this.$refs.formRef.resetFields()
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
